<template>
  <div class="footer-tap-button">
    <div class="footer-tap-content" :class="{ 'has-sidebar': hasSidebar }">
      <el-button id="reset" v-if="showCancel" type="primary" plain size="small" @click="resetHndle">{{
        $t('common.button.cancel')
      }}</el-button>
      <el-button
        id="reset"
        v-if="!showCancel && !hasOnlySubmitBtn"
        type="primary"
        plain
        size="small"
        @click="resetHndle"
        >{{ $t('common.button.reset') }}</el-button
      >
      <el-button id="previous" size="small" v-if="showPrevious" type="primary" @click="previousHandle" plain
        >{{ $t('common.button.prevent') }}
      </el-button>
      <el-button
        id="next"
        size="small"
        v-if="showNext"
        type="primary"
        :disabled="submitDisabled"
        @click="nextHandle"
        :loading="submitLoading"
        v-preventReClick
        >{{ $t('common.button.next') }}</el-button
      >
      <el-button
        id="Submit"
        size="small"
        v-else
        type="primary"
        @click="submitHanle"
        :disabled="submitDisabled"
        :loading="submitLoading"
        v-preventReClick
      >
        {{ $t('common.button.submit') }}
      </el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'footer-tap-button',
  props: {
    activeName: {
      type: String,
      default: '',
    },
    steps: {
      type: Array,
      default: () => {
        return [];
      },
    },
    submitLoading: {
      type: Boolean,
      default: false,
    },
    submitDisabled: {
      type: Boolean,
      default: false,
    },
    showCancel: {
      type: Boolean,
      default: false,
    },
    hasSidebar: {
      type: Boolean,
      default: false,
    },
    hasOnlySubmitBtn: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      inputeValue: '',
      showQuickLinks: false,
    };
  },
  computed: {
    showNext() {
      const { steps, activeName } = this;
      return steps.length && steps[steps.length - 1].name !== activeName;
    },
    showPrevious() {
      const { steps, activeName } = this;
      return steps.length && steps[0].name !== activeName;
    },
  },
  methods: {
    resetHndle() {
      this.$emit('reset');
    },
    nextHandle() {
      this.$emit('next');
    },
    previousHandle() {
      this.$emit('previous');
    },
    submitHanle() {
      this.$emit('submit');
    },
  },
};
</script>

