.footer-tap-button {
  z-index: 8;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  .footer-tap-content {
    margin: 0 92px;
    max-width: 1850px;
    min-width: 1096px;
    padding: 14px 20px;
    text-align: right;
    background-color: $--text_color--white;
    box-shadow: 0px -3px 12px 0px #00000019;
  }
  .has-sidebar {
    margin: 0 34px;
  }
}
</style>
<style lang="scss">
@media only screen and (min-width: $bigger-screen-width) {
  #app {
    .footer-tap-button {
      .footer-tap-content {
        margin: 0 auto !important;
      }
    }
  }
}
</style>
