<template>
  <portal-transfer
    v-model="selectData"
    :data="availableProduct"
    :props="transferProps"
    :titles="['Available Product', 'Selected Product']"
    filterable
    :filterPlaceholder="'Search for...'"
  >
  </portal-transfer>
</template>

<script>
import PortalTransfer from '@/components/portal-transfer';
import { queryTillProductList } from '@/views/identifier-center/api/api-till';

export default {
  name: 'SelectAssignProduct',
  components: { PortalTransfer },
  data() {
    return {
      availableProduct: [],
      selectData: [],
      transferProps: {
        key: 'productIndex',
        label: 'productName',
      },
    };
  },
  watch: {
    selectData: {
      handler(value) {
        this.$emit('getAssignProduct', value);
      },
      deep: true,
    },
  },
  created() {
    this.getProductList();
  },
  methods: {
    getProductList() {
      let params = {
        permCode: 'CREATE_TILL',
        dataPermCode: 'TILL_PRODUCT',
      };
      queryTillProductList(params).then((res) => {
        const { header, list = [] } = res;
        if (header && header.success) {
          this.availableProduct = list;
        }
      });
    },
  },
};
</script>
