<template>
  <div class="create-till">
    <div class="create-page-content" v-show="!createTill">
      <!-- title -->
      <portal-header :projectObj="titleConfig"></portal-header>
      <!-- body -->
      <el-form :model="tillFormData" ref="ruleForm" :rules="tillFormRules" label-position="top" size="small">
        <div class="identity-info" style="padding: 0 20px">
          <div>
            <div class="till-info-title-class">{{ $t('IDC.till.identityInfo') }}</div>
            <el-row :gutter="20">
              <el-col v-bind="colOptions" class="msisdn-item">
                <el-form-item
                  :label="$t('IDC.till.organizationShortCode')"
                  prop="organizationShortCode"
                  :class="{ 'show-org-short-code': checkShortCode }"
                >
                  <el-input
                    ref="shortCodeInput"
                    clearable
                    class="msisdn"
                    id="msisdn-inp"
                    v-model="tillFormData.organizationShortCode"
                    @blur="shortCodeBlue"
                    :placeholder="$t('common.placeholder.enter')"
                  >
                  </el-input>
                  <i @click="showSearch" class="el-icon-more input-search"></i>
                </el-form-item>
                <div class="org-info-box" v-if="showInformation && checkShortCode">
                  <div class="cen-test">
                    <span>{{ $t('IDC.till.organizationName') }}:</span>
                    <span>{{ organizationName }}</span>
                  </div>
                  <div class="cen-test">
                    <span>{{ $t('IDC.till.trustLevel') }}:</span>
                    <span>{{ trustLevel }}</span>
                  </div>
                </div>
              </el-col>
              <el-col v-bind="colOptions">
                <el-form-item :label="$t('IDC.till.tillNumber')" prop="tillNumber">
                  <el-button
                    type="text"
                    v-if="isShowGenerate"
                    class="view-change"
                    :underline="false"
                    @click="generate(true)"
                  >
                    {{ $t('IDC.till.generateAnotherIdentifier') }}
                  </el-button>
                  <el-input
                    class="input-wid"
                    id="alias"
                    v-model="tillFormData.tillNumber"
                    :placeholder="$t('common.placeholder.enter')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col v-bind="colOptions">
                <el-form-item prop="languageCode" :label="$t('IDC.till.language')">
                  <el-select
                    class="input-wid"
                    id="languageCode"
                    v-model="tillFormData.languageCode"
                    placeholder="Please select"
                  >
                    <el-option
                      v-for="item in languageOpt"
                      :label="item.languageName"
                      :value="item.languageCode"
                      :key="item.languageCode"
                    >
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col v-bind="colOptions">
                <el-form-item
                  :rules="defaultOperatorIDRules"
                  :label="$t('IDC.till.defaultOperatorID')"
                  prop="defaultOperatorID"
                >
                  <el-select clearable v-model="tillFormData.defaultOperatorID" placeholder="Please select">
                    <el-option
                      v-for="item in defaultOperatorIdList"
                      :label="item.label"
                      :value="item.value"
                      :key="item.value"
                    >
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col v-bind="colOptions">
                <el-form-item :label="$t('IDC.till.msisdn')" prop="msisdn">
                  <el-button type="text" class="view-change" :underline="false" @click="viewMsisdnRange">
                    {{ $t('IDC.till.viewRange') }}
                  </el-button>
                  <el-input
                    id="msisdn"
                    class="input-wid"
                    v-model="tillFormData.msisdn"
                    :placeholder="$t('common.placeholder.enter')"
                  >
                  </el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </div>
        </div>
        <el-row class="identity-info shuttle-box">
          <el-col :span="24">
            <el-form-item class="available-product-title" :label="$t('IDC.till.assignProduct')" prop="productIdList">
              <el-divider class="assign-product"></el-divider>
              <span slot="error" class="item-error">
                <div class="product-error">
                  <span>Please select at least one proudct</span>
                </div>
              </span>
            </el-form-item>
            <select-assign-product
              class="shuttleBox"
              ref="shuttleBox"
              @getAssignProduct="getAssignProduct"
            ></select-assign-product>
          </el-col>
        </el-row>
      </el-form>
      <footer-tap @reset="cancel" @submit="submitCreateTill('ruleForm')" :submit-loading="submitLoading" />
    </div>
    <portal-create-success
      v-if="createTill"
      :successText="$t('IDC.till.createSuccessText')"
      :detailText="$t('IDC.till.viewDetail') + '>>'"
      :addText="$t('common.button.continueAdd') + '>>'"
      @view-detail="goViewDetail"
      @continue-add="goContinueToAdd"
    ></portal-create-success>
    <portal-short-code
      @submitShortCode="submitShortCode"
      invoking-flag="createOrgOperator"
      :searchVisible.sync="searchVisible"
      :merchant-id.sync="tillFormData.merchantId"
      :short-code.sync="tillFormData.organizationShortCode"
      :merchantName.sync="organizationName"
      :trustLevel.sync="trustLevel"
    ></portal-short-code>
    <el-dialog
      class="msisdn-dialog"
      :close-on-click-modal="false"
      title="Till MSISDN Range"
      :visible.sync="msisdnRangeDialog"
      width="448px"
    >
      <table-page class="till-tab" :outSideTableData="msisdnRangeLists" :hasPagination="false" :columns="columns">
      </table-page>
    </el-dialog>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import SelectAssignProduct from './components/select-assign-product.vue';
import portalShortCode from '@/components/portal-short-code/index.vue';
import { createTill, loadRangeDcc, getLanguages, queryOrgOperatorIdList } from '@/views/identifier-center/api/api-till';
import {
  generateIdentifier,
  checkIdentifierBtn,
  deleteIdentifier,
} from '@/views/identifier-center/api/api-organization.js';
import PortalCreateSuccess from '@/components/portal-create-success';
import { getCreateTillRules, getDefaultOperatorIDRules } from '@/views/identifier-center/validator/till.js';
import { getOrganizationList } from '@/views/identifier-center/api/api-till';
import TablePage from '@/components/tablePage';
import footerTap from '@/components/portal-footer-tap';
export default {
  name: 'CreateTill',
  components: { PortalHeader, PortalCreateSuccess, SelectAssignProduct, TablePage, portalShortCode, footerTap },
  data() {
    return {
      preCode: '',
      organizationId: '',
      isShowGenerate: false,
      checkShortCode: true,
      showInformation: false,
      searchLoading: false,
      createTill: false,
      searchVisible: false,
      submitLoading: false,
      organizationName: '',
      trustLevel: '',
      tillID: '',
      tillFormData: {
        msisdn: '',
        tillNumber: '',
        languageCode: '',
        organizationShortCode: '',
        productIdList: [],
        defaultOperatorID: '',
        merchantId: '',
      },
      orgOperatorId: '',
      msisdnRangeDialog: false,
      msisdnRangeLists: [],
      tillFormRules: getCreateTillRules(),
      languageOpt: [],
      columns: [
        { width: '85px', label: this.$t('IDC.till.no') + '.', prop: 'index', align: 'left' },
        { label: this.$t('IDC.till.range'), prop: 'range', align: 'left' },
      ],
      defaultOperatorIdList: [
        {
          label: 'Please select',
          value: '',
        },
      ],
      defaultOperatorIDRules: [],
    };
  },
  computed: {
    titleConfig() {
      return {
        title: this.$t('IDC.till.createTill'),
        desc: this.$t('IDC.till.createTitle'),
      };
    },
  },
  methods: {
    submitShortCode() {
      this.queryOrgOperatorIdList();
      this.checkShortCode = true;
    },
    async deleteIdenti() {
      let params = {
        identifier: this.tillFormData.tillNumber || '',
        identityType: 'Till',
      };
      await deleteIdentifier(params);
    },
    async generate(val) {
      let params = {
        identityType: 'Till',
        preCode: this.tillFormData.tillNumber || '',
        parentCode: this.tillFormData.organizationShortCode || '',
        isBtnTrigger: val,
      };
      let res = await generateIdentifier(params);
      if (res.header && res.header.success) {
        this.tillFormData.tillNumber = res.identifier;
        this.preCode = res.identifier;
      }
    },
    async isShowBtn() {
      let params = {
        identityType: 'Till',
        type: '',
        parentCode: this.tillFormData.organizationShortCode || '',
      };
      let res = await checkIdentifierBtn(params);
      if (res.header && res.header.success) {
        this.isShowGenerate = res.hasRule;
        if (res.hasRule) {
          this.generate(false);
        }
      }
    },
    async shortCodeBlue() {
      let params = {
        shortCode: this.tillFormData.organizationShortCode,
      };
      let res = await getOrganizationList(params);
      if (res.header && res.header.success && res.organizationInfo) {
        this.organizationName = res.organizationInfo.organizationName;
        this.tillFormData.merchantId = res.organizationInfo.organizationId;
        this.checkShortCode = true;
        this.queryOrgOperatorIdList();
      } else {
        this.checkShortCode = false;
        this.organizationName = '';
      }
    },
    showSearch() {
      this.searchVisible = true;
    },
    closeDialog() {
      this.msisdnRangeDialog = false;
    },
    goViewDetail() {
      let params = {
        title: this.tillFormData.tillNumber,
        tillId: this.tillID,
      };
      this.$router.push({
        path: '/mainPage/identifierCenter/organizationInfo/till/view',
        query: { data: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    goContinueToAdd() {
      this.refreshCurrentPage();
    },
    submitCreateTill(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let { msisdn, tillNumber, languageCode, productIdList, organizationShortCode, defaultOperatorID } =
            this.tillFormData;
          let params = {
            organizationId: organizationShortCode,
            msisdn: msisdn,
            tillNumber: tillNumber,
            languageCode: languageCode,
            productIdList: productIdList,
            defaultOperId: defaultOperatorID || '',
          };
          this.submitLoading = true;
          createTill(params)
            .then((res) => {
              this.submitLoading = false;
              if (res.header && res.header.success) {
                this.createTill = true;
                this.tillID = res.identityId;
              }
            })
            .finally(() => {
              this.submitLoading = false;
            });
        } else {
          return false;
        }
      });
    },

    /**
     * 取消创建
     */
    cancel(closeDialog) {
      if (!closeDialog) {
        let cancel = this.$t('IDC.till.cancelOperation');
        let confirm = this.$t('common.button.confirm');
        this.$confirm(cancel, confirm, {
          confirmButtonText: 'Yes',
          cancelButtonText: 'No',
          closeOnClickModal: false,
          type: 'warning',
        }).then(() => {
          this.refreshCurrentPage();
        });
      }
    },
    viewMsisdnRange() {
      this.msisdnRangeDialog = true;
      this.initMsisdnRange();
    },
    initMsisdnRange() {
      if (this.msisdnRangeLists != null && this.msisdnRangeLists.length > 0) {
        return;
      }
      loadRangeDcc({}).then((res) => {
        if (res.missdnRanges && res.missdnRanges.length) {
          res.missdnRanges.forEach((item, index) => {
            this.msisdnRangeLists.push({
              range: item.numberBegin + ' ~ ' + item.numberEnd,
              index: index + 1,
            });
          });
        }
      });
    },
    getAssignProduct(val) {
      this.tillFormData.productIdList = val;
      this.$refs.ruleForm.validateField('productIdList');
    },
    async getLanguagesList() {
      let params = {
        dataId: 'CACHE_LANGUAGE',
        groupId: 'AG',
      };
      let res = await getLanguages(params);
      this.languageOpt = res.list;
    },
    async queryOrgOperatorIdList() {
      let params = {
        identityId: this.tillFormData.merchantId,
      };
      this.defaultOperatorIdList = [
        {
          label: 'Please select',
          value: '',
        },
      ];
      let res = await queryOrgOperatorIdList(params);
      res.orgOperatorList.forEach((item) => {
        this.defaultOperatorIdList.push({
          label: item.operatorId,
          value: item.orgOperatorId,
        });
      });
    },
    async getIdrulues() {
      this.defaultOperatorIDRules = await getDefaultOperatorIDRules();
    },
  },
  created() {
    this.initMsisdnRange();
  },
  mounted() {
    this.getLanguagesList();
    this.isShowBtn();
    this.getIdrulues();
  },
  watch: {
    'tillFormData.organizationShortCode': {
      handler() {
        this.tillFormData.organizationShortCode ? (this.showInformation = true) : (this.showInformation = false);
      },
    },
    'tillFormData.defaultOperatorID': {
      handler() {
        if (this.timer) {
          clearTimeout(this.timer);
        }
        this.timer = setTimeout(() => {
          this.$refs['ruleForm'].validateField('defaultOperatorID', () => {});
        }, 500);
      },
    },
    checkShortCode() {
      let checkShortCode = async (rule, value, callback) => {
        if (this.checkShortCode) {
          callback();
        } else {
          callback(new Error(`The Organization Short Code is invalid.`));
        }
      };
      let check = { validator: checkShortCode, trigger: 'blur' };
      this.tillFormRules.organizationShortCode.push(check);
      this.$refs.ruleForm.validateField('organizationShortCode');
    },
  },
  beforeDestroy() {
    this.deleteIdenti();
  },
};
</script>

