<template>
  <div class="portal-transfer">
    <el-transfer
      v-model="selectValue"
      :data="data"
      :props="props"
      :titles="titles"
      :filterable="filterable"
      :filter-method="filterMethod"
      :filter-placeholder="filterPlaceholder"
      v-bind="$attrs"
      v-on="$listeners"
    >
      <template v-if="customeItem" v-slot="{ option }">
        <slot name="item" :option="option"></slot>
      </template>
      <template v-slot:left-footer>
        <slot name="left-footer"></slot>
      </template>
      <template v-slot:right-footer>
        <slot name="right-footer"></slot>
      </template>
    </el-transfer>
  </div>
</template>

<script>
export default {
  name: 'PortalTransfer',
  model: {
    prop: 'value',
    event: 'select-change',
  },
  props: {
    customeItem: {
      type: Boolean,
      default: false,
    },
    data: {
      type: Array,
      default: () => [],
    },
    value: {
      type: Array,
      default: () => [],
    },
    props: {
      type: Object,
      default: () => ({}),
    },
    filterable: {
      type: Boolean,
      default: false,
    },
    titles: {
      type: Array,
      default: () => [],
    },
    filterPlaceholder: {
      type: String,
      default: 'Please select',
    },
  },
  data() {
    return {
      selectValue: [],
    };
  },
  watch: {
    selectValue: {
      handler(value) {
        this.$emit('select-change', value);
      },
      deep: true,
    },
  },
  methods: {
    filterMethod(query, item) {
      let label = item[this.props.label || 'label'] || '';
      label = label.toLowerCase();
      return label.indexOf(query) > -1;
    },
  },
};
</script>

